import styled from "styled-components"

export const BannerContainer = styled.div`
display: flex;
width:98.1%;
height: 260px;
background-color: #543929;
align-items:center;
border-bottom-right-radius: 168px;
padding-right:28px;`

export const BannerDetails = styled.div`
display: flex;
width:100%;
height: 230px;
background-color: #383736;
border-bottom-right-radius: 150px;
border-top-right-radius: 43px;
`
export const ProfileContainer = styled.div`
position:absolute;
display:flex;
width:212px;
height:212px;
padding: 5px;
border-radius: 50%;
align-items:center;
justify-content:center;
background-color:#f5f0e6;
transform: scale(1.3);
margin-top:16px;
margin-left:100px;
`
export const ProfileBio = styled.div`
display:flex;
flex-direction:column;
width:max-content;
height:Auto;
margin-left:370px;
color:#f5f0e6;
padding: 10px;
`

export const ProfileImage = styled.img`
width:210px;
height:201px;
border-radius:100%;
`
export const ProfileLink = styled.a`
display:flex;
text-decoration:none;
column-gap:5px;
align-items:center;
justify-content:center;
`

export const BodyContainer = styled.div`
display:flex;
// flex:1;
flex-wrap:wrap;
gap:45px;
margin: 30px 0px 0px 30px;
padding:25px;
`
export const ContentSection = styled.div`
display:flex;
flex-direction:column;
box-shadow:0px 2px 16px #959695;
border-radius:12px;
transition: transform 1s;
cursor:pointer;

&:hover{
    transform:scale(1.016);
    box-shadow:0px 2px 20px #959695;
}
`