import { Button } from "@mui/material"
import { Arrow, ClockICon, LocationIcon } from "../resources/assets"
import { useState } from "react"
import { CustomModal } from "../resources/modal";
import MathCertificate from "../resources/Images/Mathematics.jpg"
import Amity from "../resources/Images/Amity.jpg"
import Brainware from "../resources/Images/Brainware.jpg"

const Awards: React.FC = () => {
    const [imgUrl, setUrl] = useState('');
    const [modalFlag, setModal] = useState(false);
    return (
        <div style={{ padding: '16px' }}>
            <div style={{ display: 'flex', columnGap: '12px', alignItems: 'center' }}>
                <svg
                    xmlns="http://www.w3.org/2000/svg"
                    shapeRendering="geometricPrecision"
                    textRendering="geometricPrecision"
                    imageRendering="optimizeQuality"
                    fillRule="evenodd"
                    clipRule="evenodd"
                    viewBox="0 0 418 512.098"
                    width="50px"
                    height="50px"
                >
                    <path fill="#E37E00" d="M338.162 0l-3.226 5.303-44.592 73.121H127.443L79.838.004z" />
                    <path fill="#F60" d="M341.642 0L418 95.776l-89.519 153.682c-22.587-25.893-53.454-44.369-88.471-51.317L338.417 5.303l-.772 1.264L341.267 0h.375z" />
                    <path fill="#D0401B" d="M89.487 249.494L0 95.776 76.012.004h.242L82.8 11.187l-1.348-2.221 96.757 189.133c-35.123 6.917-66.085 25.43-88.722 51.395z" />
                    <circle fill="#F5C800" cx="209" cy="353.598" r="158.499" />
                    <path fill="#FFDD61" d="M209.112 233.909c65.819 0 119.182 53.35 119.182 119.182 0 65.837-53.363 119.201-119.182 119.201-65.83 0-119.192-53.364-119.192-119.201 0-65.828 53.362-119.182 119.192-119.182z" />
                    <path
                        fill="#E37E00"
                        fillRule="nonzero"
                        d="M212.625 285.676l18.707 43.806 47.452 4.259a3.802 3.802 0 012.179 6.652v.002l-35.898 31.346 10.619 46.463a3.814 3.814 0 01-5.802 4.041l-40.775-24.379-40.905 24.456a3.808 3.808 0 01-5.667-4.118l-.004-.002 10.62-46.462-35.9-31.345a3.807 3.807 0 012.303-6.667l47.326-4.246 18.72-43.829a3.811 3.811 0 017.025.023z"
                    />
                </svg>
                <h2>Honors And Awards</h2>
            </div>
            <div style={{ width: '100%', height: '5px', backgroundColor: '#543929', borderRadius: '5px' }}></div>
            <section>
                <section style={{ display: 'flex', flexDirection: 'column', paddingBottom: '10px', borderBottom: '0.5px solid #8d8f8d' }}>
                    <div style={{ display: 'flex', alignItems: 'center', columnGap: '10px' }}>
                        <Arrow />
                        <h3>Gold Medal in Mathematics</h3>
                        <Button sx={{
                            height: '30px',
                            borderRadius: '5px',
                            backgroundColor: '#543929',
                            color: '#ffffff',
                            textTransform: 'capitalize'
                        }} onClick={() => {
                            setUrl(MathCertificate);
                            setModal(true);
                            setTimeout(() => {
                                setModal(false);
                            }, 1);
                        }}>
                            Certificate
                        </Button>
                    </div>
                    <section style={{ display: 'flex', flexDirection: 'column', paddingLeft: '50px' }}>

                        <cite>58th City Rank in 10th International Mathematical Olympiad (IMO).</cite>

                        <cite style={{ display: 'flex', alignItems: 'center', marginTop: '10px', columnGap: '10px' }}>
                            <ClockICon />
                            December 2016
                        </cite>
                    </section>
                </section>
                <section style={{ display: 'flex', flexDirection: 'column', paddingBottom: '10px', borderBottom: '0.5px solid #8d8f8d' }}>
                    <div style={{ display: 'flex', alignItems: 'center', columnGap: '10px' }}>
                        <Arrow />
                        <h3>3rd in inter-college Techno-Innovathon</h3>
                        <Button sx={{
                            height: '30px',
                            borderRadius: '5px',
                            backgroundColor: '#543929',
                            color: '#ffffff',
                            textTransform: 'capitalize'
                        }} onClick={() => {
                            setUrl(Amity);
                            setModal(true);
                            setTimeout(() => {
                                setModal(false);
                            }, 1);
                        }}>
                            Certificate
                        </Button>
                    </div>
                    <section style={{ display: 'flex', flexDirection: 'column', paddingLeft: '50px' }}>
                        <cite style={{ display: 'flex', alignItems: 'center', marginTop: '10px', columnGap: '10px' }}
                        >
                            <LocationIcon />
                            Amity University Kolkata
                            <div style={{ width: '20px' }} />
                            <ClockICon />
                            April 2022
                        </cite>
                    </section>
                </section>
                <section style={{ display: 'flex', flexDirection: 'column', paddingBottom: '10px', borderBottom: '0.5px solid #8d8f8d' }}>
                    <div style={{ display: 'flex', alignItems: 'center', columnGap: '10px' }}>
                        <Arrow />
                        <h3>Appreciation as a Guest Speaker</h3>
                        <Button sx={{
                            height: '30px',
                            borderRadius: '5px',
                            backgroundColor: '#543929',
                            color: '#ffffff',
                            textTransform: 'capitalize'
                        }} onClick={() => { 
                            setUrl(Brainware);
                            setModal(true);
                            setTimeout(() => {
                                setModal(false);
                            }, 1);
                         }}>
                            Certificate
                        </Button>
                    </div>
                    <section style={{ display: 'flex', flexDirection: 'column', paddingLeft: '50px' }}>
                        <cite style={{ display: 'flex', alignItems: 'center', marginTop: '10px', columnGap: '10px' }}
                        >
                            <LocationIcon />
                            Brainware University, Kolkata
                            <div style={{ width: '20px' }} />
                            <ClockICon />
                            April 2021
                        </cite>
                    </section>
                </section>
            </section>
            <CustomModal url={imgUrl} openModal={modalFlag} />
        </div>
    )
}

export default Awards;