import { TabContext, TabList, TabPanel } from "@mui/lab"
import { Box, Tab } from "@mui/material"
import React, { useState } from "react";
import Portfolio from "./portFolio";
import Experience from "./experience";

const TabNavigator = () => {
    const [value, setValue] = useState('1');

    const handleChange = (event: React.SyntheticEvent, newValue: string) => {
        setValue(newValue);
    };
    return (
        <Box sx={{ width: '100%', typography: 'body1' }}>
            <TabContext value={value}>
                <Box sx={{ borderBottom: 1, borderColor: 'divider',backgroundColor:'#543929' }}>
                    <TabList onChange={handleChange} aria-label="lab API tabs example">
                        <Tab label="Portfolio" value="1"  sx={{
                            color:'white', 
                            '&.Mui-selected': {
                                color: 'white',
                              }
                              }} />
                        <Tab label="Experiences" value="2" sx={{
                            color:'white',
                            '&.Mui-selected': {
                                color: 'white', 
                              }}}/>
                    </TabList>
                </Box>
                <div style={{height:'20px', backgroundColor:'#383736'}}></div>
                <TabPanel sx={
                    {
                        padding: '0px'
                    }
                } value="1"><Portfolio /></TabPanel>
                <TabPanel
                    sx={
                        {
                            padding: '0px'
                        }
                    }
                    value="2"><Experience /></TabPanel>
            </TabContext>
        </Box>
    );
}

export default TabNavigator;