import styled from "styled-components"
import profile from '../resources/profile.jpg';
import Education from "./education";
import Industrial from "./industrial";
import Skills from "./skills";
import Projects from "./projects";
import { BodyContainer, ContentSection } from "./global";
import { ProfileBanner } from "./banner";
import Awards from "./awards";
import Certification from "./certification";

const Portfolio = () => {
    return (
        <>
            <ProfileBanner />
            <BodyContainer>
                <ContentSection style={{ width: '400px' }}>
                    <Education />
                </ContentSection>
                <ContentSection style={{ width: '800px' }}>
                    <Industrial />
                </ContentSection>
                <ContentSection style={{ width: '390px' }}>
                    <Skills />
                </ContentSection>
                <ContentSection style={{ width: '1000px' }}>
                    <Projects />
                </ContentSection>
                <ContentSection style={{ width: '600px' }}>
                    <Awards />
                </ContentSection>
                <ContentSection style={{ width: '750px' }}>
                    <Certification />
                </ContentSection>
            </BodyContainer>
        </>
    );
}

export default Portfolio;

