import { ProfileBanner } from "./banner"
import Conference from "./conference";
import { BodyContainer, ContentSection } from "./global";
import Leadership from "./leadership";
import OngoingResearch from "./ongoing";
import References from "./references";
import Research from "./research";
import Teaching from "./teaching";
import Volunteer from "./volunteer";
import Workshop from "./workshop";

const Experience: React.FC = () => {
    return (
        <>
            <ProfileBanner />
            <BodyContainer>
                <ContentSection style={{ width: '1300px' }}>
                    <Research />
                </ContentSection>
                <ContentSection style={{ width: '1300px' }}>
                    <OngoingResearch />
                </ContentSection>
                <ContentSection style={{ width: '1300px' }}>
                    <Conference />
                </ContentSection>
                <ContentSection style={{ width: '1300px' }}>
                    <Teaching />
                </ContentSection>
                <ContentSection style={{ width: '1300px' }}>
                    <Leadership />
                </ContentSection>
                <ContentSection style={{ width: '1300px' }}>
                    <Volunteer />
                </ContentSection>
                <ContentSection style={{ width: '1300px' }}>
                    <Workshop />
                </ContentSection>
                <ContentSection style={{ width: '1300px' }}>
                    <References />
                </ContentSection>

            </BodyContainer>
        </>
    )
}

export default Experience;